// #!/usr/bin/env node

'use strict';

//Cons
import { Cons } from './Cons.js';

/**
 * @class
 * @classdesc 
 * @author Keisuke Ikeda
 * @this {Table}
 */
export class Table extends Map
{
    /**
     * 
     * @constructor
     * @param {Table} aTable 
     * @return {Table} 
     */
    constructor(aTable = null)
    {
        super();
        this.source = aTable;
        this.root = (aTable == null) ? true : false; 

        return this;
    }

    /**
     * Table
     * @return {Table} Table
     */
    clone()
    {
        let aTable = new Table(this);
        for(let key of this.keys)
        {
            let value = Cons.cloneValue(this.get(key));
            if(value != null){ aTable.set(key, value); }
            else { throw new Error('RuntimeException!'); }
        }

        return aTable;
    }

    /**
     * 
     * @param {Symbol} aSymbol 
     * @return {Boolean} 
     */
    has(aSymbol)
    {
        if( super.has(aSymbol) ){ return true; }
        if(this.isRoot()){ return false; }

        return this.source.has(aSymbol);
    }
    
    /**
     * 
     * @param {*} anObject 
     * @return {Boolean} 
     */
    equals(anObject)
    {
        return super.equals(anObject);
    }

    /**
     * 
     * @return {Symbol} 
     * @return {Object} 
     */
    get(aSymbol)
    {
        if(super.has(aSymbol)){ return super.get(aSymbol); }
        if(this.isRoot()){ return null; }

        return this.source.get(aSymbol);
    }

    /**
     * 
     * @return {Boolean} 
     */
    isRoot()
    {
        return this.root;
    }

    /**
     * 
     * @param {Symbol} aSymbol 
     * @param {*} anObject 
     * @return {*} 
     */
    setIfExit(aSymbol, anObject)
    {
        let answer;
        if( super.has(aSymbol) ){ answer = this.set(aSymbol, anObject); }
        if(this.isRoot()){ answer = null; }
        else { answer = this.source.setIfExit(aSymbol, anObject); }

        return answer;
    }

    /**
     * 
     * @param {*} aBoolean 
     * @return {Null} 
     */
    setRoot(aBoolean)
    {
        this.root = aBoolean;
        return null;
    }

    /**
     * 
     * @param {Table} aTable
     * @return {Null} 
     */
    setSource(aTable)
    {
        this.source = aTable;
        return null;
    }
}
