// #!/usr/bin/env node

'use strict';

/**
 * @class
 * @classdesc Cons
 * @author Keisuke Ikeda
 * @this {Loop}
 */
export class Loop extends Object
{
    /**
     * 
     * @constructor
     * @param {Cons} aCons Cons
     * @return {Loop} 
     */
    constructor(aCons)
    {
        super();
        // Cons
        this.aCons = aCons;
        // Cons
        this.length = aCons.length();
        // 
        this.index = 1;

        return this;
    }

    /**
     * 
     * @return {Loop} 
     */
    iterator()
    {
        return this;
    }

    /**
     * 
     * @return {Boolean} 
     */
    hasNext()
    {
        return this.index <= this.length;
    }

    /**
     * 
     * @return {Object} 
     */
    next() 
    {
        let anObject = this.aCons.nth(this.index);
        this.remove();

        return anObject;
    }

    /**
     * iterator
     * for...of
     */
    [Symbol.iterator](){
        return {
            next: () => {
                if(this.index <= this.length)
                {
                    let nextValue = this.aCons.nth(this.index);
                    this.remove();
                    return { value : nextValue, done : false };
                } 
                else 
                {
                    return { done : true };
                }
            }
        }
    }
    
    /**
     * asyncIterator
     * for...of
     */
    [Symbol.asyncIterator](){
        return {
            next: () => {
                if(this.index <= this.length)
                {
                    let nextValue = this.aCons.nth(this.index);
                    this.remove();
                    return Promise.resolve({ value : nextValue, done : false });
                } 
                else 
                {
                    return Promise.resolve({ done : true });
                }
            }
        }
    }

    /**
     * 
     * @return {Null} 
     */
    remove()
    {
        this.index++;
        return null;
    }
}
