// #!/usr/bin/env node

'use strict';

/**
 * @class
 * @classdesc JavaIntStream
 * @author Keisuke Ikeda
 * @this {IntStream}
 */
export class IntStream extends Object
{
    /**
     * startafterEnd()
     * @param {Number} start 
     * @param {Number} afterEnd 
     * @return {Array} 
     */
    static range(start, afterEnd)
    {
        let end = afterEnd - 1;
        return this.rangeClosed(start, end);
    }

    /**
     * startend()
     * @param {Number} start 
     * @param {Number} afterEnd 
     * @return {Array} 
     */
    static rangeClosed(start, end)
    {
        let range = end - start + 1;
        return [... new Array(range)].map(() => start++);
    }
}
